// 引入axios  并 封装
import axios from 'axios'

import { Toast } from 'vant';

// 带配置项的axios 实例   
const instance = axios.create({
  baseURL: 'http://120.53.31.103:84',  //封装请求路径
  timeout: 2000
})

// 发送请求
instance.interceptors.request.use(function (request) {

  Toast.loading({
    message: '加载中...',
    duration:2000,
    forbidClick: true,
  });
  return request

}, function (error) {
  return Promise.reject(error)
})


// 响应 前求后  对自身服务器响应的状态码 进行处理
instance.interceptors.response.use(function (response) {
  Toast.clear()

  return response   //成功的回调
}, function (error) {
  return Promise.reject(error)
})


export default instance