<template>
  <div class="Login-box">
    <div class="Login-img">
      <img
        src="https://msmk2019.oss-cn-shanghai.aliyuncs.com/uploads/image/2019pILfAg7Avr1567732916.png"
      />
    </div>
    <van-form>
      <!-- <van-field v-model="username" @focus="input_focus()" name="手机号" placeholder="请输入手机号" />
      <van-field
        v-model="password"
        type="password"
        name="密码"
        placeholder="请输入密码"
      />-->
      <div class="lm_box">
        <div class="lm_mobile lm_input" :class="[lm_Active == 1 ? 'lm_active' : '']">
          <input
            type="text"
            v-model="mobile"
            @focus="lm_inputFcous(1)"
            @blur="lm_inputBlur()"
            placeholder="请输入手机号"
          />
          <p class="lm_toast" v-show="!lm_mobileFlag">请输入正确的手机号码</p>
        </div>
        <div class="lm_pass lm_input" :class="[lm_Active == 2 ? 'lm_active' : '']">
          <input
            type="password"
            v-model="password"
            @focus="lm_inputFcous(2)"
            @blur="lm_inputBlur()"
            placeholder="请输入密码"
          />
          <p class="lm_toast" v-show="!lm_passFlag">请输入正确的密码</p>
        </div>
        <div class="lm-text">
          <span @click="toSetPass()">找回密码</span>
          <span @click="lm_toggle()">注册/验证码登录</span>
        </div>
        <div>
          <van-button
            round
            block
            type="info"
            native-type="submit"
            color="linear-gradient(to right, rgb(255, 146, 71),rgb(253, 88, 3))"
            @click="lm_Login()"
          >登录</van-button>
        </div>
      </div>
    </van-form>
    <div></div>
  </div>
</template>

<script>
import { Login } from '../utils/Login'
export default {
  data() {
    return {
      mobile: "18568571773",
      password: "123456",
      lm_Active: null,
      lm_mobileFlag:true,
      lm_passFlag:true,
      lm_flag: false
    };
  },
  methods: {
    // /输入框获取焦点事件
    lm_inputFcous(num) {
      this.lm_Active = num;
    },
    lm_inputBlur() {
      this.lm_Active = null;
      let mobileReg = /^[1][3,4,5,7,8,9][0-9]{9}$/;
      if(mobileReg.test(this.mobile)){
        this.lm_mobileFlag = true
      }else{
        this.lm_mobileFlag = false
      }
      if(this.password.length==0){
        this.lm_passFlag = false
      }else{
        this.lm_passFlag = true
      }
      if(this.lm_mobileFlag && this.lm_passFlag){
        this.lm_flag = true
      }
    },
    lm_toggle() {
      this.$router.push("/sms-login");
    },
    // 跳转到找回密码页面
    toSetPass() {
      this.$router.push("/forget-pass");
    },
   async lm_Login() {
      if(this.lm_flag){
       let { data } = await this.$http.post("/api/app/login",{
         mobile:this.mobile,
         password:this.password,
         type:1
       })
       console.log(data);
       if(data.code == 200){
         window.sessionStorage.setItem("login_info",JSON.stringify(data.data))
         window.sessionStorage.setItem("token",JSON.stringify(data.data.remember_token))
         this.$router.push("/mine")
       }
      }else{
        this.lm_mobileFlag = false
        this.lm_passFlag = false
      }
    }
  }
};
</script>

